import { tileSize } from "../src/main"

const TILE_SIZE = 16
const DIRECTION_VELOCITY = 2 * TILE_SIZE;

export class InputManagerBind extends Phaser.Input.InputManager {
    constructor(game,keyboard){
        super(game,keyboard)
        this.keyboard = keyboard
        this.keys = [{
            name: "W", 
            value: keyboard.W,
            velocityX: 0,
            velocityY: -DIRECTION_VELOCITY,
        },
        {
            name: "S", 
            value: keyboard.S,
            velocityX: 0,
            velocityY: DIRECTION_VELOCITY,
        },
        {
            name: "D", 
            value: keyboard.D,
            velocityX: DIRECTION_VELOCITY,
            velocityY: 0,
        },
        {
            name: "A", 
            value: keyboard.A,
            velocityX: -DIRECTION_VELOCITY,
            velocityY: 0,
        }]
    }
    handleUpdate(player, mobileControls, control = null){
        if(mobileControls){
           switch (control) {
               case "top":
                    this.currentKey = this.keys[0]
                    player.play("walkUp", true)
                   
                    break;
                case "down":
                    this.currentKey = this.keys[1]
                    player.play("walkDown", true)
                   
                    break;
                case "left":
                    this.currentKey = this.keys[3]
                    player.play("walkLeft", true)
                   
                    break;
                case "right":
                    this.currentKey = this.keys[2]
                    player.play("walkRight", true)
                   
                    break;
           
               default:
                   break;
           }
           player.updatePlayerPos(this.currentKey.velocityX, this.currentKey.velocityY);

        }
        else{
            Object.keys(this.keyboard).forEach(key => {
                if(this.keyboard[key].isDown){
                    this.currentKey = this.keys.find(({ name }) => name === key);
                    switch (this.currentKey.name) {
                        case "W":
                            player.play("walkUp", true)
                            break;
                        case "A":
                            player.play("walkLeft", true)
                            break;
                        case "S":
                            player.play("walkDown", true)
                            break;
                        case "D":
                            player.play("walkRight", true)
                            break;                
                        default:
                            break;
                    }
                    player.updatePlayerPos(this.currentKey.velocityX, this.currentKey.velocityY);
                }
            })
        }
    }
    getKeys(){
        return this.keys
    }
}