import { CST } from "../CST";
import { InputManagerBind } from "../../helpers/inputManager";

export class PlayScene extends Phaser.Scene{
    constructor(){
        super({
            key: CST.SCENE.PLAY,
        })
    }
    preload()
    {
        this.load.image("leftBtn","./assets/img/game/controls/leftCtrl.png");
        this.load.image("rightBtn","./assets/img/game/controls/rightCtrl.png");
        this.load.image("topBtn","./assets/img/game/controls/topCtrl.png");
        this.load.image("bottomBtn","./assets/img/game/controls/bottomCtrl.png");
        this.load.image("terrain", "./assets/tilesetMap.png");
        this.load.tilemapTiledJSON("map", "./assets/map.json");

        this.textures.spritesheet = this.load.spritesheet("reaper", "./assets/img/game/player.png" ,{frameWidth: 16, frameHeight: 16})
        this.textures.spritesheet = this.load.spritesheet("boomSlime", "./assets/img/game/enemies/slimes/boomSlime.png" ,{frameWidth: 16, frameHeight: 16})
        this.keyboard = this.input.keyboard.addKeys("W,A,S,D");
        this.InputManagerBind = new InputManagerBind(this,this.keyboard)
    }
    create()
    {
        

        this.reaper = new Player(this, 250, 300, "reaper",this)
        // this.boomSlime = new Slime(this, 10, 50, "boomSlime", this.reaper, 40)
        this.physicsGroup = this.physics.add.group({
            // Initial angular speed of 60 degrees per second.
            // Drag reduces it by 5 degrees/s per second, thus to zero after 12 seconds.
            angularDrag: 5,
            collideWorldBounds: true,
            dragX: 150,
            dragY: 60,
        });
        this.physicsGroup.add(this.reaper)
        // Map from tiled
        this.map = this.add.tilemap("map");
        this.terrain = this.map.addTilesetImage("tilesetMap", "terrain")
        this.layer = this.map.createStaticLayer("terrainMaybe", [this.terrain], 0,0).setDepth(-1)
        this.physics.add.collider(this.reaper, this.layer)
        this.layer.setCollisionByProperty({collides:true})

        // UserInputs
        let controls = [
            {
                controlName: "left",
                input: this.add.image(this.game.renderer.width * .10, this.game.renderer.height * .90, "rightBtn")
            },
            {
                controlName: "right",
                input: this.add.image(this.game.renderer.width * .30, this.game.renderer.height * .90, "leftBtn")
            },
            {
                controlName: "top",
                input: this.add.image(this.game.renderer.width * .20, this.game.renderer.height * .85, "topBtn")
            },
            {
                controlName: "bottom",
                input: this.add.image(this.game.renderer.width * .20, this.game.renderer.height *.95, "bottomBtn")
            }
        ]
        controls.forEach(control => {
            control.input.setInteractive();
            control.input.on('pointerdown',()=>
            {
                this.InputManagerBind.handleUpdate(this.reaper,true,control.controlName)
            })
        })

        
    }
    update()
    {
        this.InputManagerBind.handleUpdate(this.reaper)
    }
}

class Player extends Phaser.Physics.Arcade.Sprite {
    constructor(scene, x = 0, y = 0, texture = 'dude', currentScene) {
      super(scene, x, y, texture)
      this.velocity = {
        x: 0,
        y: 0,
      };
      this.texture = texture
      this.currentScene = currentScene
      scene.add.existing(this)
      scene.physics.add.existing(this)
      scene.events.on('create', this.create, this)
      scene.events.on('update', this.update, this)
    }
    create()
    {
        // Set Player's animations

        this.currentScene.anims.create({
            key: "walkDown",
            frameRate: 5,
            // repeat: -1,
            frames: this.currentScene.anims.generateFrameNumbers("reaper", { start: 0, end: 0 }),
        });   
          this.currentScene.anims.create({
            key: "walkUp",
            frameRate: 5,
            // repeat: -1,
            frames: this.currentScene.anims.generateFrameNumbers("reaper", { start: 1, end: 1 }),
        });   
        this.currentScene.anims.create({
            key: "walkLeft",
            frameRate: 5,
            // repeat: -1,
            frames: this.currentScene.anims.generateFrameNumbers("reaper", { start: 2, end: 2 }),
        });   
          this.currentScene.anims.create({
            key: "walkRight",
            frameRate: 5,
            // repeat: -1,
            frames: this.currentScene.anims.generateFrameNumbers("reaper", { start: 3, end: 3 }),
        });   
        this.setScale(2)
    }
    update()
    {
        
    }
    updatePlayerPos(velocityX, velocityY){
        this.velocity.x = velocityX;
        this.velocity.y = velocityY;
        this.setVelocity(this.velocity.x, this.velocity.y);
    }
}

class Slime extends Phaser.Physics.Arcade.Sprite {
    constructor(scene, x = 0, y = 0, texture = 'boomSlime', target, speed) {
      super(scene, x, y, texture, target, speed)
        this.target = target
        this.speed = speed;
        scene.add.existing(this)
        scene.physics.add.existing(this)
        scene.events.on('update', this.update, this)
        }
    
    create()
    {
        this.anims.create({
            key: "walk",
            frameRate: 5,
            repeat: -1,
            frames: this.anims.generateFrameNumbers(texture, { start: 0, end: 0 }),
        });
        this.slime = this.add.sprite(100,100,texture)
    }
    update()
    {
       
    }
}